:root {
    --nav-link-hover-bg-color: #161616;
    --nav-link-hover-color: white;
    --inline-search-border-color: var(--main-blue);
    --name-box-bg-color: var(--main-blue);
    --name-box-text-color: white;

    --main-blue: #04179B;
    --main-light-blue: #071EC9;
    --main-green: #4AE54A;
    --black: #161616;
    --darkGold: rgb(231, 175, 69);
    --offPink: rgb(228, 170, 194);
    --lightPink: rgb(245, 220, 220);

    --head: 'ubuntu-bold';
    --text: 'ubuntu-medium';
 
 }

 h1, h2, h3, h4, h5, h6 {
     font-family: var(--head);
 }

 hr {
     color: rgba(50, 54, 47, 0.2);
 }
 
 body{
     font-family: var(--text);
     margin:0px;
     font-size: 1rem;
     color: var(--black);
 }
/* 
 a:visited {
     color: unset;
 } */

 a.active {
     color: white;
     background-color: var(--main-blue);
 }

header {
    padding: 5px 20px;
    color: var(--main-blue);
    box-shadow: 0px 0px 3px rgba(0,0,0,0.3);
}
.logo {
    font-size: 1.5rem;
}
nav {
    transition: ease;
}
nav a {
    color: var(--black);
    padding: 2px 5px;
    letter-spacing: 1px;
}
nav a:hover {
    color: var(--main-blue);
}
.search {
    padding: 2px 5px;
    color: var(--black);
}
.search:hover {
    color: var(--main-blue);
}
.name-box {
    padding: 5px 10px;
    line-height: 40px;
    max-height: 40px;
}
section {
    margin: 0px;
    padding: 5px 20px;
}
.section-cover {
    position: relative;
    min-height: 92vh;
}
.quote {
    position: absolute;
    top: 10%;
    max-width: 1000px;
}
.quote > h3 {
    font-size: 50px;
    margin-bottom: 10px;
}
.quoter {
    color: #04179B;
    font-weight: bold;
}
@media (max-width: 600px) {
    .quote{
        position: relative;
    }
    .quote > h3 {
        font-size: 45px;
        margin-bottom: 20px;
    }
}
.featured-in {
    min-height: 200px;
    background-color: var(--darkGold);
    bottom: 0;
    border-radius: 5px;
    text-align: center;
    padding: 5px;
    width: 100%;
}
.featured-in > h1 {
    letter-spacing: 2px;
    font-family: 'ubuntu-bold-italic';
    color: white;
}
.stack h1, .stack h2, .stack h3, .stack h4, .stack h5, .stack h6 {
    letter-spacing: 2px;
    font-size: 20px;
    color: #161616;
}
.stack {
    align-items: center;
}
.stack-item {
    display: inline-block;
    margin: 35px 5vw;
    vertical-align: middle;
}
.get-started {
    position: absolute;
    top: 70%;
    width: 1000px;
}
.get-started > p {
    font-size: 20px;
    letter-spacing: 2px;
    font-family: 'ubuntu-regular';
    display: inline-block;
}
@media(max-width: 600px) {
    .get-started {
        position: relative;
        margin: 10px 0px;
        width: 100%;
    }
}
.hire {
    padding: 15px 20px;
    display: inline-block;
    background-color: var(--main-blue);
    color: white;
    font-size: 15px;
    margin: 10px;
}
.jpc.detail-card .content > div > div {
    margin-bottom: 30px;
}
.detail-card .image {
    background-color: whitesmoke;
}
.operations {
    text-align: center;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}
footer {
    background-color: var(--black);
    color: rgb(245, 220, 220);
    font-style: normal;
    padding: 5px 20px;
}
footer h4 {
    letter-spacing: 2px;
    color: var(--darkGold);
}
.subscribe-input {
    width: 200px;
}
.subscribe-input:hover {
    background-color:white;
    border-color:  var(--main-light-blue);
}
.subscribe-button {
    padding: 5px 10px;
    background-color: rgb(231, 175, 69);
    color: var(--black);
}
.div {
    display: flex;
    justify-content: space-between;
}
.div > div {
    max-width: 400px;
    min-width: 200px;
}
.img-form:hover {
    background-color: white !important;
}
footer .desc {
    margin: 50px 0px 10px 0px;
    text-align: center;
}
footer .link {
    margin: 15px auto
}
footer .link a {
    color: var(--offPink);
    letter-spacing: .5px;
    margin-left: 10px;
}
footer .link a:hover {
    color: var(--lightPink);
}